
<template>
  <v-container>
    <h3>邮件列表</h3>
    <!-- 此处的$route.params.msg表示从路由/msg传递过来的参数 -->
    
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-card-title>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="查找"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :headers="headers"
            :items="domains"
            :search="search"
          ></v-data-table>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
 
<script>
import axios from 'axios';
export default {

  name: "vTyWorkSpace",

  data: () => ({
    
    search : '',
    headers:[
    { text: '域名', value: 'domain' },
    { text: 'mx', value: 'mx' },
    { text: 'spf', value: 'spf' },
    { text: 'dmarc', value: 'dmarc' },
    { text: 'ptr', value: 'ptr' },
    { text: 'uid', value: 'uid' },
    { text: '收件数量', value: 'mailcount' },
    ],

    domains:[
      {domain:"hi"},
      {domain:"h2"},
    ],
  
  }),
  methods:{
    addNewDomain(event){
        console.log(event);
        this.$router.push({ name: "vPageEditDomain", params: { msg: 'new' } });
    },
    _helpMxStr(mx){
        console.log(mx);
        mx = JSON.parse(mx)
        let ss = '';
        for( let k in mx){
            ss = ss + k ;
            ss = ss + ":" + mx[k];
        }
        return ss;
    }
  },
  mounted: function(){
    let alt = 2;
    if (alt==1) return;
    axios.defaults.baseURL = process.env.VUE_APP_BASEURL;
    axios.get('/api/getDomains' ).then( response=>{ 
      console.log("/getDomains", response);
      this.domains = [];
      if ( response.status == 200 && response.data.code == 200){
        let mylist = response.data.data;
        for ( let i in mylist){
          console.log( "item", mylist[i] );
          let citem = {};
          citem.domain = mylist[i].domain;
          citem.mailcount = mylist[i].recv_count;
          citem.mx = this._helpMxStr(mylist[i].dns_mx)
          
          this.domains.push( citem );
        }
      }
      //for(const i in rep)
      
    }).catch(err=>{
      console.log(err);
    });
  }
};

</script>

